import React, { useState, useEffect } from "react";
import { Carousel } from "react-responsive-carousel";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import { Close as CloseIcon } from "@mui/icons-material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const ModalContent = ({ item }) => {
  const [imageLoaded, setImageLoaded] = useState(false);

  useEffect(() => {
    const preloadImage = new Image();
    preloadImage.src = `https://image.tmdb.org/t/p/w500/${item.backdrop_path}`;
    preloadImage.onload = () => {
      setImageLoaded(true);
    };
  }, [item.backdrop_path]);

  return (
    <div>
      <div
        style={{
          position: "relative",
          width: "100%",
          paddingTop: "66.67%", // 3:2 aspect ratio
          overflow: "hidden",
          boxShadow: "0 0 0 1px #dadce0",
          display: imageLoaded ? "block" : "none",
        }}
      >
        <img
          src={`https://image.tmdb.org/t/p/w500/${item.backdrop_path}`}
          alt="Backdrop Image"
          style={{
            position: "absolute",
            top: "0",
            left: "0",
            width: "100%",
            height: "100%",
            objectFit: "cover",
          }}
        />
      </div>
      <h2
        style={{
          margin: "1rem",
          overflow: "hidden",
          textOverflow: "ellipsis",
          display: "-webkit-box",
          WebkitLineClamp: "2",
          WebkitBoxOrient: "vertical",
          color: "#70757a",
        }}
      >
        {item.title}
      </h2>
      <p
        style={{
          margin: "1rem",
          textAlign: "left",
          color: "#70757a",
          display: "inline-block",
        }}
      >
        {item.overview.length > 100
          ? item.overview.slice(0, 100) + "... "
          : item.overview}
        {item.overview.length > 100 && (
          <a
            style={{
              color: "#007BFF",
              display: "inline-flex",
              alignItems: "center",
            }}
            onClick={() => {
              // Handle read more functionality
            }}
          >
            more <ExpandMoreIcon />
          </a>
        )}
      </p>
    </div>
  );
};

const ModalComponent = ({
  selectedItemIndex,
  handleModalClose,
  handleModalNavigation,
  likedItems,
  drawerWidth,
}) => {
  const handleChangeSelectedItem = (index) => {
    handleModalNavigation(index);
  };

  return (
    <Dialog
      open={selectedItemIndex !== null}
      onClose={handleModalClose}
      classes={{ paper: "custom-modal-paper" }}
      PaperProps={{
        style: {
          margin: "0",
          height: "100vh",
          position: "fixed",
          right: "0",
          top: "0",
          maxHeight: "unset",
          maxWidth: "unset",
        },
      }}
    >
      <div style={{ width: drawerWidth, position: "relative" }}>
        {selectedItemIndex !== null && (
          <>
            <IconButton
              style={{
                position: "absolute",
                top: "16px",
                right: "16px",
                color: "#fff",
                zIndex: "1",
                backgroundColor: "rgb(255, 255, 255, 0.1)",
              }}
              onClick={handleModalClose}
            >
              <CloseIcon />
            </IconButton>
            <Carousel
              selectedItem={selectedItemIndex}
              onChange={handleChangeSelectedItem}
              showThumbs={false}
              showStatus={false}
              showIndicators={false}
              swipeable
              style={{ willChange: "transform" }}
            >
              {likedItems.map((item, index) => (
                <div key={index}>
                  <ModalContent item={item} />
                </div>
              ))}
            </Carousel>
          </>
        )}
      </div>
    </Dialog>
  );
};

export default ModalComponent;
